/**
 * @template Item
 * @template Metadata
 * @template {'header'|'items'} ChunkType
 * @param {ChunkType} type Chunk type
 * @param {Item} base First item in the chunk
 * @param {Metadata} metadata Chunk metadata
 * @returns {{ metadata: Metadata, type: ChunkType, items: Item[], base: Item }} Chunk
 */
const createChunk = (type, base, metadata) => {
    return {
        type,
        base,
        items: [base],
        metadata,
    };
};

/**
 * @template Item
 * @template Metadata
 * @template {'header'|'items'} ChunkType
 * @param {Item[]} items Items to stream
 * @param {(item: Item) => Metadata} extractor Chunk metadata generator
 * @yields {undefined|{ metadata: Metadata, type: ChunkType, items: Item[], base: Item }} Chunked items stream
 */
export default function* chunkedStream(items, extractor) {
    let pos = 0;
    let item;
    /**
     * @member {undefined|{ metadata: Metadata, type: ChunkType, items: Item[], base: Item }} chunk
     */
    let chunk;

    while ((item = items[pos++])) {
        const metadata = extractor(item);

        if (chunk) {
            // check if we should start new chunk
            const isHeader = metadata !== chunk?.metadata;

            if (isHeader) {
                yield chunk;

                chunk = undefined;
            }
        }

        if (chunk) {
            // add to current chunk
            chunk.items.push(item);
        } else {
            // start new chunk
            yield createChunk('header', item, metadata);
            chunk = createChunk('items', item, metadata);
        }
    }

    if (chunk) {
        // yield leftovers
        yield chunk;
    }
}
