const getMatcher = (part) => (value) => String(value).toLowerCase().includes(part);

export const byTerm = (value) => {
    const match = value.toLowerCase();

    if (match.startsWith('#')) {
        // filter by order number or tag
        const tagOrOrder = match.slice(1);
        const order = +tagOrOrder;

        if (Number.isNaN(order)) {
            const isMatching = getMatcher(tagOrOrder);

            // filter by tag
            return (item) => item.tags?.some(isMatching);
        }

        return (item) => !order || (item.order === order);
    }

    const isMatching = getMatcher(match);

    return (item) => {
        if (item.data.some(({ value }) => isMatching(value))) {
            return true;
        }

        if (item.notes && isMatching(item.notes)) {
            return true;
        }
    };
};
