import PropTypes from 'prop-types';
import React from 'react';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';

import { useActions } from '@mssgme/hooks';
import { Button, LoadingIndicator } from '@mssgme/ui';

import { useDebouncedList } from '../../../../hooks';

import styles from './AudienceList.scss';

const fields = [
    'name',
    'email',
    'phone',
];
const byTerm = (value) => {
    const match = value.toLowerCase();

    return (item) => {
        return fields.some((field) => String(item[field]).toLowerCase().includes(match));
    };
};

export const AudienceList = ({ websiteId }) => {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const store = useSelector((store) => store.audience);

    const { isFetching, items, hasMore, handleLoadMore } = useDebouncedList(store, { term: byTerm }, (request) =>
        api.websites.audience.list({ websiteId, ...request })
    );

    return (
        <div>
            <ul className={styles.list}>
                {items.map((item) => (
                    <li key={item._id} title={item.name} className={styles.item}>
                        <div className={styles.name}>{item.name}</div>
                        <div className={styles.details}>
                            {item.email && (
                                <span>{item.email}</span>
                            )}
                            {item.phone && (
                                <span>{item.phone}</span>
                            )}
                        </div>
                    </li>
                ))}
            </ul>

            {!items.length &&
                (isFetching ? (
                    <LoadingIndicator color="#2979ff" />
                ) : (
                    <div className={styles.emptyMessage}>
                        <h3>{t('Website.View.Audience.labels.emptyMessage')}</h3>
                    </div>
                ))}

            {hasMore && (
                <Button fullWidth className={styles.more} isFetching={isFetching} onClick={handleLoadMore}>
                    {t('Website.View.Audience.labels.loadMore')}
                </Button>
            )}
        </div>
    );
};

AudienceList.propTypes = {
    websiteId: PropTypes.string.isRequired,
};
