import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { debounce } from 'lodash';

import { useFilter } from '@mssgme/hooks';

export const useDebouncedFilter = (initial, setter, debouncedFilters = []) => {
    const debouncedSet = useMemo(() => debounce((f) => setter(f), 400), [setter]);

    const [filter, setFilter] = useState(initial);
    const isDebounced = useRef(false);

    const handleFilter = useCallback(({ target: { name, value } }) => {
        setFilter((old) => (old[name] === value ? old : { ...old, [name]: value }));

        if (debouncedFilters.includes(name)) {
            isDebounced.current = true;
        }
    }, []);

    useEffect(() => {
        if (isDebounced.current) {
            isDebounced.current = false;
            debouncedSet(filter);

            return () => debouncedSet.cancel();
        }

        setter(filter);
    }, [filter, setter]);

    useEffect(() => () => setter(null), [setter]);

    return {
        filter,
        setFilter,
        handleFilter,
    };
};

/**
 * @template T
 * @param {object} config Config
 * @param {T[]} config.items List items
 * @param {string} [config.next] Next item token
 * @param {boolean} [config.isFetching] Request is fetching
 * @param {object} config.filter Current filter state
 * @param {number} config.guid Request GUID, used to ignore obsolete request results
 * @param {object} filters Filters to apply in useFilter
 * @param {(data: object) => Promise<{ results: T[]; next?: string; hasNext?: boolean }>} fn Request method
 */
export const useDebouncedList = (config, filters, fn) => {
    const ref = useRef(fn);
    const items = useFilter(config.items, config.filter, filters);
    const filter = useMemo(
        () => Object.fromEntries(Object.entries(config.filter).filter(([, v]) => !!v)),
        [config.filter]
    );

    ref.current = fn;

    const handleLoadMore = useCallback(() => {
        if (config.isFetching || !config.next) {
            return;
        }

        void ref.current({ query: { ...filter, next: config.next } });
    }, [filter, config.next, config.isFetching]);

    useEffect(() => {
        void ref.current({
            guid: config.guid + 1,
            query: filter,
        });
    }, [config.guid, filter]);

    return {
        isFetching: config.isFetching,
        hasMore: !!config.next,
        items,
        handleLoadMore,
    };
};
